<template>
	<view class="user-login">
		<view class="user_all">
			<view class="user_logo">
				<image src="https://fucai.yiruit.com/UpFiles/tupian/logo.png" mode="aspectFit"></image>
			</view>
			<view class="user_info">
				<!-- 注册 -->
				<view class="input-wrap" v-if="isRegister">
					<view class="user-input">
						<view class="input_put">
							<image src="https://fucai.yiruit.com/UpFiles/tupian/user.png" mode="widthFix"></image>
						</view>
						<input type="text" placeholder="请输入真实姓名" v-model="realname" placeholder-style="color:#fff" />
					</view>
					<view class="user-input">
						<view class="input_put first_icon">
							<image src="https://fucai.yiruit.com/UpFiles/tupian/tel.png" mode="widthFix"></image>
						</view>
						<input type="text" maxlength="11" placeholder="请输入手机号" @blur='inputTel' v-model="phone" placeholder-style="color:#fff" />
					</view>
					<view class="user-input">
						<view class="input_put">
							<image src="https://fucai.yiruit.com/UpFiles/tupian/yanzhengma.png" mode="widthFix"></image>
						</view>
						<input type="text" placeholder="请输入验证码" v-model="verification" placeholder-style="color:#fff" />
						<view class="huoqu" @click="handleCode">
							<text>{{dataTime}}</text>
						</view>
					</view>
					<view class="user-input" v-if="isFormal==2">
						<view class="input_put">
							<image src="https://fucai.yiruit.com/UpFiles/tupian/yaoqingma.png" mode="widthFix"></image>
						</view>
						<input type="text" placeholder="请输入邀请码" maxlength="6" @blur='invitationChang' v-model="invitation" placeholder-style="color:#fff" />
					</view>
					<view class="user-store-btn">
						<view class="user_title">
							<text @click="handleFanhui">返回登录</text>
						</view>
					</view>
				</view>
				<!-- 登录 -->
				<view v-else class="input-wrap">
					<view class="user-input">
						<view class="input_put first_icon">
							<image src="https://fucai.yiruit.com/UpFiles/tupian/tel.png" mode="widthFix"></image>
						</view>
						<input type="text" maxlength="11" placeholder="请输入手机号" v-model="phone" @blur='inputTelChange' placeholder-style="color:#fff" />
					</view>
					<view class="user-input">
						<view class="input_put">
							<image src="https://fucai.yiruit.com/UpFiles/tupian/yanzhengma.png" mode="widthFix"></image>
						</view>
						<input type="text" placeholder="请输入验证码" v-model="verification" placeholder-style="color:#fff" />
						<view class="huoqu" @click="handleCode">
							<text>{{dataTime}}</text>
						</view>
					</view>
					<view class="user-store-btn">
						<view class="user_title">							
							<text @click="handleUser">新用户注册</text>
						</view>
					</view>
				</view>
			</view>
		</view>

		<view class="user_bg">
			<image src="../../static/bg.png" mode="widthFix"></image>
		</view>			
		<view class="user_btn" :class="{user_logo:active}">
			<button v-if="isRegister == false" @click="handleGetUserInfo">登录</button>
			<button v-else open-type="getUserInfo" @getuserinfo="handleRegiter">注册</button>
		</view>
		<view v-if="isFormal === 2">			
			<view class="handle" :class="{handle_logo:active}">
				<text @click="handleJump('./merchantLogin')">商家登录</text>
				<text @click="handleJump('./riderLogin')">骑手登录</text>
				<text @click="handleJump('./intimidateLogin')">打单员登录</text>
			</view>
		</view>			
		<view class="btn_bg">
			<image src="https://fucai.yiruit.com/UpFiles/tupian/title.png" mode="aspectFit"></image>
		</view>
	</view>
</template>

<script>
	import common from "../../common/common";
	var _self;
	export default {
		data() {
			return {
				realname: "",
				phone: "",
				verification: "", // 验证码
				invitation:'',  // 邀请码
				isRegister: false,
				userInfo: {},
				city: "",
				active:false,
				dataTime:'获取验证码',
				isFormal:1 
			};
		},
		onLoad() {
			_self = this;
			_self.stypeQuest(); // 判断是否显示
			_self.getAdress(); // 经纬度		
			
		},
		methods: {
			stypeQuest(){
				common.request({
					method:"GET",
					path: "login/getversion",
					success: res => {  	
						_self.isFormal = res.data.Version;
					}
			    })
			},			
			handleFanhui(){  // 返回用户登录
				_self.isRegister = false;
				_self.active = false
			},			
			invitationChang(){
				common.request({   // 注册时判断邀请码填写是否正确
					path: "login/panduan",
					method: "GET",
					data: parseFloat(_self.invitation),
					success: res => {
						if(res.code === 200){
							uni.showToast({
								icon: 'none',
								title: '邀请码正确',
								duration: 1500,
							})
						}else{
							uni.showToast({
								icon: 'none',
								title: '邀请码错误',
								duration: 1500,
							})
							_self.invitation = ''
						}
					}
				})	
			},	
			inputTel() { // 手机号的判断	
				if (!common.checkPhone(_self.phone)) {
					uni.showToast({
						icon: 'none',
						title: '请输入正确的手机号码',
						duration: 1500,
					});	
					_self.phone = ''
				}else{
					//号码输入正确  则再次判断手机号是否注册过
					common.request({   // 用户再次输入手机号 登录  判断是否注册过
						path: "login/selectphone",
						method: "GET",
						data: {
							mobile : _self.phone	// 手机号		
						},
						success: res => {
							if(Object.keys(res.data).length !== 0 ){
								uni.showToast({
									icon: 'none',
									title: '该用户已注册',
									duration: 1500,
								})
								_self.phone = ''							
							}
						}
					})	
				}
			},
			// 用户注册
			handleRegiter() {
				if (_self.city != "湘潭市") {
					uni.showToast({
						title: '您不在湘潭市，暂时无法注册登陆',
						icon: 'none',
						duration: 1500,
						mask: true,
					});
					return;
				}	
				// 姓名  手机号  验证码  邀请码                                                    手机号再次判断
				if (_self.realname && _self.phone && _self.verification && _self.invitation) {	
					// 首页初始化后台给的openId					
					let openId = uni.getStorageSync("openid");
					uni.login({
						provider: 'weixin',
						success(loginResult) {
							uni.getUserInfo({
								provider: 'weixin',
								success: function(infoRes) {
									_self.userInfo = infoRes.userInfo;									
									_self.userLogin(_self.userInfo.nickName, openId,_self.phone, _self.realname, _self.userInfo.avatarUrl,_self.invitation, _self.userInfo.gender);
								},
								fail: () => {
									uni.showToast({
										title: '未授权'
									});
								}
							})
						}
					})
				} else {
					uni.showToast({
						icon: 'none',
						title: '请输入正确信息',
						duration: 1500,
					});
				}
			},
			// 用户注册的接口
			userLogin(nickName, openId, phone, realname, avatarUrl, invitation, gender) {
				common.request({
					path: "login/loginbyopenid",
					method: "GET",
					data: {
						nickName: nickName, //微信名
						openId: openId,
						phone: phone,
						realname: realname, //真实姓名
						avatarUrl: avatarUrl, // 头像
						invite: invitation, //邀请码
						gender: gender // 性别
					},
					success: result => {
						uni.showToast({
							title: "注册成功",
							duration: 1500,
							mask: true
						});
						uni.setStorageSync("userinfo", result.data);
						common.setstate(result.data);
						uni.switchTab({
							url: "../index/index"
						});
					}
				});
			},
			// 获取用户的经纬度
			getAdress() {
				uni.getLocation({
					type: "gcj02",
					success(res1) {
						const latitude = res1.latitude;
						const longitude = res1.longitude;
						uni.setStorageSync("jingwei", {
							latitude,
							longitude
						});
						wx.request({
							url: `https://apis.map.qq.com/ws/geocoder/v1/?location=${latitude},${longitude}&key=5XTBZ-SXL6R-7OYWX-WTOCF-M7BUK-TSFBQ`,
							data: {},
							header: {
								"content-type": "application/json"
							},
							method: "GET",
							dataType: "json",
							responseType: "text",
							success: result => {
								_self.city = result.data.result.address_component.city;
							}
						});
					},
					fail: err => {
					}
				});
			},		
			handleUser() { // 新用户
				_self.isRegister = true;
				_self.active = true;
			},			
			handleJump(url){ // 跳转
				uni.navigateTo({
					url: url
				})
			},
			
			inputTelChange(){  //用户再次登录判断手机号是否注册过  同时注册账号时也可用
				common.request({   // 用户再次输入手机号 登录  判断是否注册过
					path: "login/selectphone",
					method: "GET",
					data: {
						mobile : _self.phone	// 手机号				
					},
					success: res => {							
						if(Object.keys(res.data).length === 0 ){
							uni.showToast({
								icon: 'none',
								title: '该用户不存在',
								duration: 1500,
							})
							_self.phone = ''							
						}
					}
				})	
			},	
			handleGetUserInfo() {   // 用户登录
				if( _self.phone && _self.verification){					
					common.request({   // 用户再次输入手机号 登录  判断是否注册过	
						path: "login/selectphone",
						method: "GET",
						data: {
							mobile : _self.phone,
							code : _self.verification
						},
						success: res => {					
							if(Object.keys(res.data).length !==0 ){		
								uni.setStorageSync("userinfo", res.data);
								common.setstate(res.data);
								uni.switchTab({
									url: "../index/index"
								})	
							}else{
								uni.showToast({
									icon: 'none',
									title: '该用户不存在',
									duration: 1500,
								})
							}
						}
					})			
				}else{
					uni.showToast({
						icon: 'none',
						title: '请输入正确信息',
						duration: 1500,
					})
				}
			},
			handleCode(){  // 获取验证码		
				if(_self.phone){	// 判断手机是否填写 正确					
					let time = 60
					var timeId = setInterval(()=>{		
						if(time == 0) {
							_self.dataTime = '获取验证码'	
							clearInterval(timeId)
							return 
						} 
						time --
						_self.dataTime = time + 's'				
					},1000)
				}
			}
		}
	};
</script>
<style lang="scss" scoped>
	.user-login {
		height: 100vh;
		overflow: hidden;
		background-color: #fff;

		.user_bg {
			margin-bottom: 40rpx;
		}

		.user_all {
			background-color: #ff3c35;
		}
	}

	.user_all {
		.user_logo {
			padding-top: 50rpx;

			image {
				width: 200rpx;
				height: 200rpx;
				display: block;
				margin: auto;
			}
		}
		.user_info {
			width: 80%;
			margin-top: 60rpx;
			padding: 0 10%;
		}
	}
	.user_btn {
		width: 100%;
		position: fixed;
		bottom: 320rpx;
		button {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 80%;
			height: 80rpx;
			background-color: #ff3c35;
			border-radius: 20rpx;
			color: #fff;
			font-size: 28rpx;
		}
	}
	.user_logo{
		bottom: 220rpx;
	}
		
	.handle{
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		width: 100%;
		position: fixed;
		bottom: 200rpx;
		text{
			color: #999;
		}
	}
	.handle_logo{		
		bottom: 140rpx;
	}
	.btn_bg {
		position: fixed;
		width: 100%;
		bottom: 20rpx;
		text-align: center;
		image {
			height: 80rpx;
			margin: auto;
		}
	}

	.user_info {
		.input-wrap {
			display: flex;
			justify-content: center;
			align-items: center;
			display: flex;
			flex-direction: column;

			.user-input {
				width: 100%;
				height: 70rpx;
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				border-bottom: 1px solid #eee;
				margin-bottom: 40rpx;

				.input_put {
					width: 50rpx;
					height: 60rpx;
					margin-right: 10rpx;
					padding-top: 10rpx;

					image {
						display: block;
						width: 80%;
					}
				}

				.huoqu {
					width: 160rpx;
					height: 60rpx;
					line-height: 60rpx;					
					text-align: center;
					border-radius: 10rpx;					
					text{
						font-size: 12px;
						color: #fff;
					}
				}

				.first_icon {
					image {
						width: 70%;
					}
				}

				input {
					flex: 1;
					height: 64rpx;
					background: none;
					color: #fff;
				}
			}

			.user-btn {
				width: 100%;
				text-align: right;
				margin-top: -24rpx;
				display: flex;
				flex-direction: row;
				justify-content: flex-end;

				text {
					color: #fff;
					font-size: 14px;
				}
			}

			.user-store-btn {
				width: 100%;
				.user_title {
					display: flex;
					flex-direction: row;
					justify-content: flex-end;
				}
				text {
					color: #fff;
					font-size: 14px;
				}
			}
		}
	}
</style>
